import React from 'react';
import 'antd/dist/antd.css';
import { Input, Button, Icon, Modal,Popconfirm,Divider,Table} from 'antd';
import {Link} from 'react-router-dom';
import Highlighter from 'react-highlight-words';
import WrappedRegistrationForm from './components/RegistrationForm';
import api from '../../Global/Api';


class hotelRoomAmenities extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      hotelroomamenities: [],
      searchedColumn: '',
      hotelroomamenitiesdetail:{},
      isEditProduct: false,
    };
  }

 onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  componentDidMount() {
    this.fetch();
  }

  fetch = () => {
    this.setState({ loading: true });
    api.get(`/api/hotelroomamenities`,{
      }).then(res => {
        this.setState({
          loading: false,
          hotelroomamenities: res.data,
        });
      });
  };

  handleChange= async (id)=>{
    this.setState({ loading:true});
    await api.get(`/api/hotelroomamenities/${id}`, {})
    .then(res => {
      this.setState({
        hotelroomamenitiesdetail: res.data,
        visible: true,
        loading:false,
        isEditProduct:true
      });
    });
   };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,isEditProduct:false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,isEditProduct:false,
    });
  };

  getColumnSearchProps = dataIndex => ({
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              this.searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
            style={{ width: 188, marginBottom: 8, display: 'block' }}
          />
          <Button
            type="primary"
            onClick={() => this.handleSearch(selectedKeys, confirm)}
            icon="search"
            size="small"
            style={{ width: 90, marginRight: 8 }}
          >
            Search
          </Button>
          <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
        </div>
      ),
      filterIcon: filtered => (
        <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
      ),
      onFilter: (value, record) =>
        record[dataIndex]
          .toString()
          .toLowerCase()
          .includes(value.toLowerCase()),
      onFilterDropdownVisibleChange: visible => {
        if (visible) {
          setTimeout(() => this.searchInput.select());
        }
      },
      render: text => (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ),
  });


  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };

  handleDelete= async (id)=>{
    this.setState({ loading:true});
    await api.delete(`/api/hotelroomamenities/${id}`, {});
    this.fetch();
    this.setState({loading:false});
   };


   render() {

    const columns = [{
      dataIndex: 'id',
      title: 'HotelAmenties Id',
      key:'Id',
      defaultSortOrder: 'ascend',
      sorter: (a, b) => a.id - b.id,
      },
      {
        dataIndex: 'amenity_name',
        title: 'Amenties Name',
        key: 'amenity_name',
        ...this.getColumnSearchProps('amenity_name'),
      },
      {
        dataIndex: 'amenity_status',
        title: 'Status',
        key: 'amenity_status' ,
        render: val => (val ? 'True' : 'False')
      },
      {
        dataIndex: 'id',
        title: 'Action',
        key: 'action',
        render: (text,record) => (
          <span>
            <Link to={`/hotel/amenitiesform/${record.id}`}>Edit</Link>
            <Divider type="vertical"/>
            <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.id)}>
              <a>Delete</a>
           </Popconfirm>
          </span>
          ),

      }
    ];

    const { loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;
    return (
      
      <div>
        <div style={{ marginBottom: 16 }}>
          <Button type="primary"  disabled={!hasSelected} loading={loading}>
            Delete
          </Button>
          <Button href="/hotel/amenitiesform/" type="primary" style={{float:"right"}}>
           Create
          </Button>
          <Modal
            title="Hotel Room Amenities Form"
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
          <WrappedRegistrationForm isEditProduct={this.state.isEditProduct} hotelroomamenitiesdetail={this.state.hotelroomamenitiesdetail}    fetch={this.fetch} handleOk={this.handleOk}/>
          </Modal>
          <span style={{ marginLeft: 8 }}>
            {hasSelected ? `Selected ${selectedRowKeys.length} items` : ''}
          </span>
        </div>
        <Table bordered rowSelection={rowSelection} columns={columns} dataSource={this.state.hotelroomamenities} scroll={{ x: 'max-content' }}/>
      </div>
    );
  }
}
 
export default hotelRoomAmenities;





