import React from 'react';
import api from '../../../Global/Api';
import { Form,Input,Radio,Button,Modal,Upload,Icon} from 'antd';

  
class RegistrationForm extends React.Component {
  constructor(props){
    super(props);
    this.initialState = {
      id:'',
      amenity_name:'',
      amenity_status:'',
      amenity_image:'',
    };
    if(this.props.hotelroomamenitiesdetail){
      this.state = props.hotelroomamenitiesdetail
    } else {
      this.state = this.initialState;
    }

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  success() {
    Modal.success({
      content: 'Successively Updated',
    });
  }
  
  error() {
    Modal.error({
      title: 'This is an error message',
      content: 'Error Occurred!!',
    });
  }
  

    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, value) => {
        if (!err) {
          console.log('Received values of form: ', value);
          this.setState({loading:true});
          if(this.props.isEditProduct){
          api.put(`/api/hotelroomamenities/${this.props.hotelroomamenitiesdetail.id}`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 200){
                  this.success();
                  this.props.fetch();
              }
            })
            .catch(error => {
              this.error();
            });
          }
            else{
              api.post(`/api/hotelroomamenities`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 201){
                  this.success();
                  this.props.fetch();
              }
            })
            .catch(error => {
              this.error();
            });
            }
          this.props.handleOk();
          this.setState(this.initialState);
         }
      });
    };

    componentWillReceiveProps(props){
    
      if(props.hotelroomamenitiesdetail){
        this.state = props.hotelroomamenitiesdetail;
      } else {
        this.state = this.initialState;
      }
    }


    render() {
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Form.Item label="Hotel Amentity">
            {getFieldDecorator('amenity_name', { initialValue: this.state.amenity_name,
              rules: [
                {
                  required: true,
                },
              ],
            })(<Input />)}
          </Form.Item>

        <Form.Item label="Amenity Image" extra="Upload Photo">
          {getFieldDecorator('amenity_image', { initialValue: this.state.amenity_image,
            valuePropName: 'fileList',
            getValueFromEvent: this.normFile,
          })(
            <Upload name="amenity_image" action="/upload.do" listType="picture">
              <Button>
                <Icon type="upload" /> Click to upload
              </Button>
            </Upload>,
          )}
        </Form.Item>
          
          <Form.Item label="Status" hasFeedback {...formItemLayout}>
            {getFieldDecorator('amenity_status', { initialValue: this.state.amenity_status,
              rules: [
                {
                  required: true,
                  type: 'boolean',
                },
              ],
            })(
              <Radio.Group name="radiogroup">
                <Radio value={true}>
                  Active
                </Radio >
                <Radio value={false}>
                  Disable
                </Radio >
              </Radio.Group>
            )}
          </Form.Item>
          

          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
  const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
   
  export default WrappedRegistrationForm;
